@import "../../libs/styles/main.scss";

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1116px;
  margin: 0 auto 40px;
}

.buttonWrapper {
  width: calc((100% - 72px) / 3);

  @include breakpoint(mobile) {
    width: 100%;
    padding: 0 $general-spacing-unit * 4;
  }
}
