.wrapper {
  background: #f0f7f7;
  align-items: center;
  flex-direction: column;
  padding: 32px 0;
  display: flex;
  margin: 0 0 24px;
}

.label {
  font-weight: 700;
  font-size: 18px;
  color: #222b45;
  text-align: center;
  line-height: 24px;
  margin: 0 0 16px;
  display: block;
}

.select {
  height: 56px;
  width: 348px;
  font-size: 18px;
  font-weight: 600;
  color: #222a46;
  border-radius: 10px;
  font-family: "Source Sans Pro";
  border-color: #e4e9f2;
  padding: 0 16px;
  appearance: none;
  background-color: #f7f9fc;
  background-position: 310px center;
  background-repeat: no-repeat;
}
