@import "../../libs/styles/main.scss";

.wrapper {
  padding: 0 32px;
  box-sizing: border-box;
  border-radius: 24px;
  box-shadow: 0 2px 20px 0 rgba(27, 155, 158, 0.1),
    0 2px 20px 0 rgba(151, 151, 151, 0.1);
  background: #fff;
  width: 1092px;
  max-width: 100vw;
  margin: 0 auto;

  @include breakpoint("mobile") {
    border-radius: 0;
    padding: 0 $general-spacing-unit * 3;
  }
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 1116px;
  margin: 0 auto 32px;

  @include breakpoint("mobile") {
    margin-bottom: 0;
    padding: $general-spacing-unit * 3;
    padding-left: $general-spacing-unit;
  }
}

.detail {
  flex: 1;
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  color: #222b45;
  text-align: center;
  padding-left: 24px;
}

.profileWarning {
  padding: 32px 32px 24px;
  border-bottom: 1px solid #efefef;
  text-transform: uppercase;
  font-weight: 700;
  color: #1b9b9e;
  margin: 0 0 24px -32px;
  font-size: 16px;
  width: calc(100% + 64px);
  box-sizing: border-box;

  @include breakpoint("mobile") {
    width: 100vw;
    padding: $general-spacing-unit * 3 $general-spacing-unit * 3;
    margin-left: -$general-spacing-unit * 3;
  }
}

.descriptionWrapper {
  color: #222b45;
}

.descriptionWrapper p {
  font-size: 16px;
  line-height: 24px;
  margin: 24px 0;
}

.wrapper h1 {
  font-weight: 700;
  font-size: 24px;
  color: #222b45;
  margin: 0 0 8px;
}

.date {
  margin: 0;
  font-size: 16px;
  color: #222b45;
  line-height: 1em;
}

.date b {
  font-weight: 700;
}

.footer {
  padding: 16px 32px 32px;
  border-top: 1px solid #efefef;
  margin: 0 0 24px -32px;
  box-sizing: border-box;
  font-size: 16px;
  width: calc(100% + 64px);

  @include breakpoint("mobile") {
    padding: $general-spacing-unit * 3 $general-spacing-unit * 3;
    margin-left: -$general-spacing-unit * 3;
    width: 100vw;
  }
}

.footer p {
  font-weight: 700;
  font-size: 14px;
  color: #222b45;
  line-height: 20px;
  margin: 8px 0 24px;
}

.footer p a {
  color: #1b9b9e;
  text-decoration: none;
}

.backButton {
  background: transparent;
  border: 0;
  height: 56px;
  width: 56px;
  cursor: pointer;
}
