@import "../../libs/styles/main.scss";

.link {
  text-decoration: none;
  color: #616264;
  text-align: center;
  margin-left: 32px;
  opacity: 0.9;
  transition: color ease 0.25s, opacity ease 0.25s;

  &:first-child {
    margin-left: 0;
  }

  &:hover {
    opacity: 1;
    color: #000;
  }

  @include breakpoint(mobile) {
    opacity: 1;

    &:last-child {
      padding-right: $general-spacing-unit * 3;
    }
  }
}

.text {
  margin-top: 16px;
  display: block;
  font-weight: 600;
  font-size: 12px;
  line-height: 12px;
}

.icon {
  width: 64px;
  height: 64px;
  display: block;
}
