@import "../../libs/styles/main.scss";

.button {
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  border: 2px solid transparent;
  border-radius: 100%;
  transition: opacity ease 0.15s;

  @include breakpoint("desktop") {
    &:hover {
      opacity: 0.5;
    }
  }
}

.linkText {
  display: none;
}
