@import "../../libs/styles/main.scss";

.wrapper {
  text-align: center;
  margin: 0 0 $desktop-vertical-spacing;

  @include breakpoint(mobile) {
    margin-bottom: $mobile-vertical-spacing;
    box-sizing: border-box;
    padding: 0 $mobile-side-spacing;
  }
}

.description {
  font-size: 20px;
  color: #253c45;
  text-align: center;
  line-height: 32px;
  max-width: 566px;
  margin: 24px auto;
}

.locations {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 24px 0 0;
  padding: 0;
  font-size: 18px;
  color: #253c45;
  line-height: 28px;

  @include breakpoint("tablet") {
    padding: 0 $mobile-side-spacing * 2;
  }

  li {
    display: block;
    margin: 0 $general-spacing-unit * 2;
    white-space: nowrap;

    @include breakpoint(mobile) {
      margin: $general-spacing-unit * 0.5 $general-spacing-unit;
    }
  }

  li:before {
    content: "";
    background: #1b9b9e;
    display: inline-block;
    height: 8px;
    width: 8px;
    border-radius: 4px;
    transform: translateY(-2px);
    margin-right: $general-spacing-unit;
  }
}
