@import "../../libs/styles/main.scss";

.wrapper {
  display: flex;
  flex-direction: row;
  position: relative;
  width: 100vw;
  padding-left: calc((100vw - 1092px) / 2);
  box-sizing: border-box;
  margin: 120px auto;

  @include breakpoint("mobile-and-tablet") {
    flex-direction: column-reverse;
    margin: 0 0 $mobile-side-spacing;
  }
}

.leftColumn {
  width: calc((1092px / 2) - 40px);
  margin-right: 40px;

  @include breakpoint("mobile-and-tablet") {
    width: auto;
    margin: 0 $mobile-side-spacing;
  }
}

.rightColumn {
  flex: 1;
  margin-left: 40px;
  position: relative;

  @include breakpoint("mobile-and-tablet") {
    margin: 0 0 40px;
  }
}

.image {
  object-fit: cover;
  object-position: center;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  flex: 1;
  width: 47vw;

  @include breakpoint("mobile-and-tablet") {
    position: static;
    width: 100%;
    height: auto;
  }

  @include breakpoint("tablet") {
    height: 40vw;
  }
}
