.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 1092px;
  margin: 0 auto;
  box-sizing: border-box;
}

.title {
  font-weight: 700;
  font-size: 70px;
  color: #222b45;
  line-height: 70px;
  margin: 0 0 16px;
}

.title b {
  color: #1b9b9e;
}

.description {
  font-weight: 500;
  font-size: 24px;
  color: #222b45;
  line-height: 32px;
  margin: 0;
}

.image {
  margin-left: 120px;
  height: 570px;
  width: 600px;
  object-fit: contain;
}

@media only screen and (max-width: 1279px) {
  .wrapper {
    padding: 0 48px;
  }

  .title {
    font-size: 32px;
    line-height: 38px;
    margin: 0 0 8px;
  }

  .description {
    font-size: 16px;
    line-height: 24px;
  }

  .image {
    margin-left: 16px;
    height: 316px;
    width: 336px;
  }
}

@media only screen and (max-width: 440px) {
  .wrapper {
    flex-direction: column;
    text-align: center;
    padding: 0 32px;
  }

  .description {
    margin: 16px 0 24px;
  }
}
