@import "../../libs/styles/main.scss";

.wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba($color-cloud-burst, 0.25);
  z-index: 3;
  visibility: hidden;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  &.isOpen {
    opacity: 1;
    visibility: visible;
  }
}

.container {
  border-radius: 40px;
  background: #fff;
  flex: 0;
  padding: 58px 64px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 2px 50px 0 rgba(27, 155, 158, 0.1),
    0 2px 20px 0 rgba(151, 151, 151, 0.1);
  z-index: 3;
  visibility: hidden;
  opacity: 0;
  width: 960px;
  max-width: calc(100vw - 80px);
  box-sizing: border-box;

  @include breakpoint(mobile) {
    border-radius: $general-spacing-unit * 2;
    box-sizing: border-box;
    width: calc(100vw - #{$general-spacing-unit * 4});
    max-width: none;
    padding: $general-spacing-unit * 3;
  }

  &.isOpen {
    opacity: 1;
    visibility: visible;
  }

  p {
    margin: 0;
  }
}

.closeButton {
  font-size: 0;
  position: absolute;
  right: 32px;
  top: 24px;
  width: 40px;
  height: 40px;
  border: 0;
  background: transparent;
  padding: 0;
  cursor: pointer;

  @include breakpoint(mobile) {
    width: $general-spacing-unit * 4;
    height: $general-spacing-unit * 4;
    right: $general-spacing-unit * 2;
    top: $general-spacing-unit * 3;
  }

  svg {
    transform: rotate(45deg);
  }
}

.title {
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  margin: 0 0 24px;

  @include breakpoint(mobile) {
    max-width: calc(100% - 40px);
  }
}

.url {
  font-weight: 700;
  font-size: 16px;
  color: #1b9b9e;
  line-height: 140%;
  max-width: 100%;
  overflow: hidden;
  display: block;
  text-overflow: ellipsis;

  @include breakpoint(mobile) {
    font-size: 14px;
  }
}

.actions {
  margin: 72px 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @include breakpoint(mobile) {
    flex-direction: column-reverse;
    margin-top: $general-spacing-unit * 3;
  }

  & > button {
    max-width: 220px;
    margin: 0;

    @include breakpoint(mobile) {
      margin-top: $general-spacing-unit * 3;
      max-width: none;
    }
  }
}

.socialLinks {
  flex-direction: row;
  display: flex;

  @include breakpoint(mobile) {
    overflow-x: scroll;
    overflow-y: hidden;
    margin-left: $general-spacing-unit * -3;
    margin-right: $general-spacing-unit * -3;
    padding-left: $general-spacing-unit * 3;
  }
}
