@import "../../libs/styles/main.scss";

.wrapper {
  width: 1092px;
  margin: 0 auto;
  padding: 120px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: border-box;

  @include breakpoint("tablet") {
    width: 75%;
    flex-direction: column-reverse;
    text-align: center;
    padding: 80px 0;
  }

  @include breakpoint(mobile) {
    width: calc(100vw - 80px);
    flex-direction: column-reverse;
    text-align: center;
    padding: 80px 0;
  }
}

.descriptionWrapper {
  font-weight: 500;
  font-size: 18px;
  color: #253c45;
  line-height: 28px;
  margin-top: 24px;
}

.image {
  height: 590px;

  @include breakpoint(large) {
    margin-left: 56px;
  }

  @include breakpoint("tablet") {
    width: 75%;
    height: auto;
    margin-bottom: $general-spacing-unit * 7;
  }

  @include breakpoint(mobile) {
    width: 100%;
    height: auto;
    margin: 0 0 32px;
  }
}
