.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f7f9fc;
  border-radius: 20px;
  padding: 24px;
  box-shadow: 0 2px 20px 0 rgb(27 155 158 / 5%),
    0 2px 16px 0 rgb(151 151 151 / 10%);
}

.figure {
  border-radius: 100%;
  padding: 10px;
  border: 2px solid #1b9b9e;
  margin: 0 0 16px;
}

.avatar {
  border-radius: 100%;
  display: block;
  width: 100px;
  height: 100px;
}

.quote {
  font-size: 16px;
  line-height: 24px;
  font-style: italic;
  color: #2e3a59;
  margin-bottom: 24px;
  text-align: center;
  flex: 1;
}

.name {
  font-size: 13px;
  line-height: 17px;
  color: #1b9b9e;
  text-align: center;
  font-weight: 700;
  margin: 0;
}

.description {
  font-size: 13px;
  line-height: 17px;
  color: #222b45;
  text-align: center;
  font-weight: 600;
  margin: 0;
}
