.button {
  border-radius: 5px;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  flex: 1;
  text-decoration: none;
  display: block;
  box-sizing: border-box;
  white-space: nowrap;
  background: #1b9b9e;
  color: #fff;
  height: 56px;
  line-height: 52px;
  padding: 0 56px;
  border: none;
  cursor: pointer;
  font-family: "Source Sans Pro";
  margin-top: 24px;
  width: 100%;
  border: 2px solid #1b9b9e;
  transition: color ease 0.15s, background-color ease 0.15s;
}

.button:hover {
  background: #fff;
  color: #1b9b9e;
}

.button.outlined {
  background: transparent;
  color: #fff;
  border: 2px solid #fff;
  line-height: 52px;
}

.button.disabled {
  cursor: not-allowed;
  opacity: 0.75;
}
