@import "../../libs/styles/main.scss";

.wrapper {
  height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  background: transparent center center no-repeat;
  background-size: cover;
  position: relative;

  @include breakpoint("tablet") {
    padding: 0 $general-spacing-unit * 7;
  }

  &:before,
  &:after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 0;
  }

  &:before {
    background: rgba(#000, 0.3);
  }

  &:after {
    height: 50%;
    background: linear-gradient(rgba(#000, 0), rgba(#000, 1));
  }
}

.content {
  text-align: center;
  z-index: 1;
  color: #fff;

  @include breakpoint(mobile) {
    max-width: calc(100vw - 80px);
  }

  button {
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;

    &:hover {
      background: $color-white;
      color: $color-cloud-burst;
    }

    @include breakpoint(mobile) {
      width: auto;
      height: 64px;
    }
  }
}

.title {
  font-weight: 700;
  font-size: 70px;
  line-height: 70px;
  max-width: 810px;
  margin: 0 0 16px;

  @include breakpoint("tablet") {
    font-size: 56px;
    line-height: 56px;
  }

  @include breakpoint(mobile) {
    font-size: 32px;
    line-height: 40px;
  }
}

.description {
  font-weight: 500;
  font-size: 24px;
  text-align: center;
  line-height: 32px;
  margin: 16px 0 56px;

  @include breakpoint("tablet") {
    max-width: 75%;
    margin-left: auto;
    margin-right: auto;
  }

  @include breakpoint(mobile) {
    font-size: 18px;
    line-height: 24px;
  }
}

.scrollButton {
  position: absolute;
  bottom: 56px;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
  width: 72px;
  height: 72px;
  border-radius: 72px;
  border: 2px solid #fff;
  box-sizing: border-box;
  background: transparent;
  cursor: pointer;
  transition: background-color ease 0.25s;

  &:hover {
    background-color: $color-white;
  }

  @include breakpoint(mobile) {
    display: none;
  }

  svg {
    width: 30%;
    height: auto;
  }
}
