@import "../../libs/styles/main.scss";

.wrapper {
  border-top: 16px solid #e7df46;
  border-bottom: 152px solid #fbfcfd;
  background: #1b9b9e;
  color: #fff;
  position: relative;

  @include breakpoint(mobile) {
    border-bottom: none;

    &:after {
      content: "";
      display: block;
      width: 100%;
      height: $desktop-vertical-spacing * 2 + $general-spacing-unit * 2;
      background: $color-catskill;
      bottom: 0;
      left: 0;
      position: absolute;
      z-index: 0;
    }
  }
}

.header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-between;
  margin: 80px auto $desktop-vertical-spacing;
  width: 1092px;
  max-width: calc(100vw - 56px - 56px);

  @include breakpoint(mobile) {
    margin: 0;
    padding: $mobile-side-spacing $mobile-side-spacing $mobile-side-spacing *
      0.5;
  }
}

.controls {
  margin-bottom: $general-spacing-unit * 2;
}

.button {
  border: 2px solid #fff;
  background: transparent;
  height: 56px;
  width: 56px;
  border-radius: 56px;
  box-sizing: border-box;
  padding: 0;
  cursor: pointer;
  margin-left: 24px;
  outline: none;
  transition: opacity ease 0.25s;
  transform: rotate(90deg);

  &[disabled] {
    opacity: 0.3;
    cursor: default;
  }

  & + & {
    transform: rotate(270deg);
  }

  @include breakpoint(mobile) {
    margin-top: $general-spacing-unit * 3;
    margin-left: 0;
    margin-right: $general-spacing-unit * 2;
  }
}

.slider {
  margin-left: calc((100vw - 1092px - (56px / 2)) / 2);
  margin-bottom: -152px;
  display: block;
  z-index: 1;
  position: relative;

  @include breakpoint("tablet") {
    margin-left: $general-spacing-unit * 5;
  }

  @include breakpoint(mobile) {
    margin: 0;

    > div > div > div {
      padding-bottom: $general-spacing-unit * 2;
    }
  }
}

.itemWrapper {
  max-width: 304px;
  margin: 16px 16px;

  @include breakpoint(mobile) {
    max-width: none;
    margin: 0;
    padding: 0 $general-spacing-unit;
    box-sizing: border-box;
  }
}
