@import "../../libs/styles/main.scss";

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  max-width: 1116px;
  margin: 0 auto;

  @include breakpoint(tablet) {
    padding: $general-spacing-unit * 1.5;
  }
}

.column {
  padding: 12px;
  box-sizing: border-box;
  width: calc(100% / 3);

  @include breakpoint(tablet) {
    width: calc(100% / 2);
  }

  @include breakpoint(mobile) {
    padding: $general-spacing-unit * 1.5 $general-spacing-unit * 4;
    width: 100%;
  }
}
